// 引入封装的post请求
import { postHttp,getHttp} from "@/utils/http.js";

// 用户登录
let userLogin = (data)=>{
    return new Promise((reslove,reject)=>{
        postHttp("login",data).then(res=>{
            reslove(res)
        })
    })
}

//获取左侧菜单权限
let getMenus = ()=>{
    return getHttp("menus")
}
export {
    userLogin,
    getMenus
}